import { Routes, Route, useNavigate } from 'react-router-dom'
import {
    DesktopOutlined,
    FileOutlined,
    PieChartOutlined,
    UserOutlined
} from '@ant-design/icons';
import { Breadcrumb, Layout, Menu } from 'antd';
import React, { useState } from 'react';
import AddArticle from './AddArticle'
import ArticleList from './ArticleList'
const { Content, Footer, Sider } = Layout;

function getItem(label, key, icon, children) {
    return {
        key,
        icon,
        children,
        label,
    };
}

const items = [
    getItem('', '1', <PieChartOutlined />),
    getItem('', '2', <DesktopOutlined />),
    getItem('', 'sub1', <UserOutlined />, [
        getItem('', 'addArticle'),
        getItem('', 'articleList')
    ]),
    getItem('', '9', <FileOutlined />),
];

const AdminIndex = () => {
    const navigate = useNavigate()
    const [collapsed, setCollapsed] = useState(false);

    const handleClickArticle = (e) => {
        console.log(e.key)
        if (e.key == 'articleList'){
            navigate('/index/list')
        }else {
            navigate('/index/add')
        }
    }
    return (
        <Layout
            style={{
                minHeight: '100vh',
            }}
        >
            <Sider collapsible collapsed={collapsed} onCollapse={(value) => setCollapsed(value)}>
                <div className="logo" />
                <Menu theme="dark" defaultSelectedKeys={['1']} mode="inline" items={items} 
                    onClick={handleClickArticle}
                />
            </Sider>
            <Layout className="site-layout">
                <Content
                    style={{
                        margin: '0 16px',
                    }}
                >
                    <Breadcrumb
                        style={{
                            margin: '16px 0',
                        }}
                    >
                        <Breadcrumb.Item></Breadcrumb.Item>
                        <Breadcrumb.Item></Breadcrumb.Item>
                    </Breadcrumb>
                    <div
                        className="site-layout-background"
                        style={{
                            padding: 24,
                            minHeight: 360,
                        }}
                    >
                        <div>
                            <Routes>
                                <Route path="" element={<AddArticle />} />
                                <Route path="add/" element={<AddArticle />} />
                                <Route path="add/:id" element={<AddArticle />} />
                                <Route path="list/" element={<ArticleList />} />
                            </Routes>
                        </div>
                    </div>
                </Content>
                <Footer
                    style={{
                        textAlign: 'center',
                    }}
                >
                    Ant Design 2022 Created by SteveWen
          </Footer>
            </Layout>
        </Layout>
    );
};

export default AdminIndex;