import React, { useState, useEffect } from 'react';
import { marked } from 'marked'
import { Row, Col, Input, Select, Button, DatePicker, message } from 'antd'
import '../static/css/AddArticle.css'
import TextArea from 'antd/lib/input/TextArea';
import { useNavigate, useParams } from 'react-router-dom'
import axios from 'axios'
import servicePath from '../config/apiUrl'

const { Option } = Select;
marked.setOptions({
    renderer: new marked.Renderer(),
    langPrefix: 'hljs language-', // highlight.js css expects a top-level 'hljs' class.
    pedantic: false,
    gfm: true,
    breaks: false,
    sanitize: false,
    smartLists: true,
    smartypants: false,
    xhtml: false
});

const AddArticle = (props) => {
    const {id} = useParams() //useNavigate
    const navigate = useNavigate()
    
    const [articleId, setArticleId] = useState(0) //Id00
    const [articleTitle, setArticleTitle] = useState('') //
    const [articleMd, setArticleMd] = useState('') //markdown
    const [articleHtml, setArticleHtml] = useState('') //html
    const [introduceMd, setIntroduceMd] = useState('') //markdown
    const [introduceHtml, setIntroduceHtml] = useState('')
    const [showDate, setShowDate] = useState() //
    const [updateDate, setUpdateDate] = useState('')//
    const [typeInfo, setTypeInfo] = useState([]) //
    const [selectedType, setSelectedType] = useState(1) //

    //
    const getTypeInfo = () => {
        axios({
            method: 'get',
            url: servicePath.getTypeInfo,
            header: { 'Access-Control-Allow-Origin': '*' },
            withCredentials: false
        }).then(
            res => {
                console.log(' res.data.data', res.data.data)
                if (res.data.data == "") {
                    localStorage.removeItem('openId')
                    navigate('/')
                } else {
                    setTypeInfo(res.data.data)
                }
            }
        ).catch((error) => {
            console.log('', error)
        }).then(
            console.log('getTypeInfo finished')
        )
    }

    useEffect(() => {
        getTypeInfo()
        const tmpId = id
        if(tmpId) {
            setArticleId(tmpId)
            getArticleById(tmpId)
        }
    }, [id])

    const changeArticleMd = (e) => {
        setArticleMd(e.target.value)
        let html = marked(articleMd)
        setArticleHtml(html)
    }
    const changeintroduceMd = (e) => {
        setIntroduceMd(e.target.value)
        let html = marked(introduceMd)
        setIntroduceHtml(html)
    }

    //
    const selectTypeHandler = (value) => {
        setSelectedType(value)
    }

    //
    const saveArticle = () => {

        if (!selectedType) {
            message.error('')
        } else if (!articleTitle) {
            message.error('')
        } else if (!articleMd) {
            message.error('')
        } else if (!introduceMd) {
            message.error('')
        } else if (!showDate) {
            message.error('')
        } else {
            message.success('')
            let dataProps = {}
            dataProps.type_id = selectedType
            dataProps.title = articleTitle
            dataProps.introduce = introduceMd
            dataProps.article_content = articleMd
            //let dateText = showDate.replace('-','/')
            //dataProps.addTime = `${(new Date(dateText).getTime())/1000}`
            dataProps.addTime = showDate
    
    
            if (articleId == 0) {
                console.log('dataProps=: ' + dataProps)
                dataProps.view_count = Math.ceil(Math.random() * 100) + 1000
                axios({
                    method: 'post',
                    url: servicePath.addArticle,
                    data: dataProps,
                    withCredentials: false
                }).then(
                    res => {
                        setArticleId(res.data.insertId)
                        if (res.data.isSuccess) {
                            message.success('')
                        } else {
                            message.error('')
                        }
                    }
                )
            } else {
                dataProps.id = articleId
                axios({
                    method: 'post',
                    url: servicePath.updateArticle,
                    header: { 'Access-Control-Allow-Origin': '*' },
                    data: dataProps,
                    withCredentials: false
                }).then(res => {
                    if (!res.data.isSuccess) {
                        message.success('')
                    } else {
                        message.error('')
                    }
                })
            }
        }
    }

    const getArticleById = (id) => {
        axios({
            method: 'get',
            url: servicePath.getArticleById + id,
            header:{ 'Access-Control-Allow-Origin':'*' }
        }).then(
            res => {
                let articleInfo = res.data.data[0]
                console.log(articleInfo)
                setArticleTitle(articleInfo.title)
                setArticleMd(articleInfo.content)
                let html = marked(articleInfo.content)
                setArticleHtml(html)
                setIntroduceMd(articleInfo.introduce)
                let introhtml = marked(articleInfo.introduce)
                setIntroduceHtml(introhtml)
                setShowDate(articleInfo.addTime)
                setSelectedType(articleInfo.typeId)
            }
        )
    }


    return (
        <div>
            <Row gutter={16}>
                <Col span={18}>
                    <Row gutter={10}>
                        <Col span={20}>
                            <Input
                                placeholder="" value={articleTitle} size="large" onChange={(e) => setArticleTitle(e.target.value)}>
                            </Input>

                        </Col>
                        <Col span={4}>
                            <Select defaultValue={selectedType} onChange={selectTypeHandler} size="large" >
                                {typeInfo.map((item, index) => {
                                    return (
                                        <Option key={index} value={item.id}>{item.typeName}</Option>
                                    )
                                })}
                            </Select>


                        </Col>
                    </Row>
                    <br />
                    <Row gutter={10}>
                        <Col span={12}>
                            <TextArea className="markdown-content" rows={35}
                                placeholder=""
                                value={articleMd}
                                onChange={changeArticleMd}
                            >

                            </TextArea>

                        </Col>
                        <Col span={12}>
                            <div className="show-html"
                                dangerouslySetInnerHTML={{ __html: articleHtml }}
                            >
                            </div>
                        </Col>
                    </Row>
                </Col>
                <Col span={6}>
                    <Row>
                        <Col span={24}>
                            <Button size="large"></Button>
                            <Button style={{ margin: 5 }}
                                size="large" type="primary"
                                onClick={saveArticle}
                            ></Button>
                        </Col>
                        <Col span={24}>
                            <textarea
                                rows={4}
                                onChange={changeintroduceMd}
                                value={introduceMd}
                                placeholder=""
                            ></textarea>
                            <div className="introduce-html"
                                dangerouslySetInnerHTML={{ __html: introduceHtml }}
                            ></div>
                        </Col>

                        <Col span={12}>
                            <div className="date-select">
                                <DatePicker
                                    placeholder=""
                                    size="large"
                                    onChange={(date, dateString) => setShowDate(dateString)}
                                >
                                </DatePicker>
                            </div>
                        </Col>
                    </Row>
                </Col>
            </Row>
        </div>
    )
}

export default AddArticle